import React from "react";
import io from "socket.io-client";
import DartsScorer from "../DartsScorer";

const SocketProvider = ({ roomCode }) => {
  const [socket, setSocket] = React.useState(null);
  const [state, setState] = React.useState(null);

  React.useEffect(() => {
    const socket = io();
    socket.emit("join", roomCode);

    socket.on("state", (data) => {
      setState(data);
    });

    setSocket(socket);
  }, [roomCode]);

  const sendToMirror = (state) => {
    socket.emit("state", { state, roomCode });
  };

  return <DartsScorer sendToMirror={sendToMirror} state={state} />;
};

export default SocketProvider;
