import React from "react";
import MarkboardRow from "./MarkboardRow";

const Markboard = ({ state, clickDart }) => {
  return (
    <div className="darts-scorer-markboard">
      {[20, 19, 18, 17, 16, 15, "B"].map((sectorValue, idx) => {
        return (
          <MarkboardRow
            key={idx}
            sector={idx}
            name={sectorValue}
            m0={state.players[0].marks[idx]}
            m1={state.players[1].marks[idx]}
            clickDart={clickDart}
            activePlayer={state.currentThrow}
          />
        );
      })}
    </div>
  );
};

export default Markboard;
