import React from "react";
import Header from "./Header";
import Markboard from "./Markboard";
import CurrentTurn from "./CurrentTurn";
import { newMatchState, nextLegState, applyTurn, undoTurn, setNames } from "./logic";

import "./index.css";

const DartsScorer = (props) => {
  const [matchState, _setMatchState] = React.useState(props.state || newMatchState());
  const [currentTurn, setCurrentTurn] = React.useState([]);

  const setMatchState = (state) => {
    const newState = JSON.parse(JSON.stringify(state));
    _setMatchState(newState);
    props.sendToMirror(newState);
  };

  React.useEffect(() => {
    props.state && _setMatchState(props.state);
  }, [props]);

  React.useEffect(() => {
    const keyHandler = (event) => {
      switch (event.key) {
        case "Enter":
          confirmTurnHandler();
          event.preventDefault();
          break;

        case "Backspace":
          eraseDartHandler();
          break;

        default:
          break;
      }
    };

    window.addEventListener("keydown", keyHandler);
    return () => {
      window.removeEventListener("keydown", keyHandler);
    };
  }, [matchState, currentTurn]);

  const clickDartHander = (dart) => {
    if (matchState.complete) {
      return;
    }
    if (currentTurn.length < 3) {
      setCurrentTurn(currentTurn.concat(dart));
    }
  };

  const undoTurnHandler = () => {
    const { state, turn } = undoTurn(matchState);
    setMatchState(state);
    setCurrentTurn(turn);
  };

  const eraseDartHandler = () => {
    if (matchState.complete) {
      return;
    }
    setCurrentTurn(currentTurn.slice(0, -1));
  };

  const confirmTurnHandler = () => {
    if (matchState.complete) {
      return;
    }

    const turn = [{ miss: true }, { miss: true }, { miss: true }].map((miss, idx) => {
      return currentTurn[idx] || miss;
    });

    setMatchState(applyTurn(matchState, turn));
    setCurrentTurn([]);
  };

  const nextLegHandler = () => {
    setMatchState(nextLegState(matchState));
    setCurrentTurn([]);
  };

  const setNamesHandler = (n1, n2) => {
    setMatchState(setNames(matchState, n1, n2));
  };

  return (
    <div className="darts-scorer-container">
      <Header state={matchState} setNames={setNamesHandler} />
      <Markboard state={matchState} clickDart={clickDartHander} />
      <CurrentTurn
        currentThrowerName={matchState.names[matchState.currentThrow]}
        isComplete={matchState.complete}
        darts={currentTurn}
        undoTurn={undoTurnHandler}
        eraseDart={eraseDartHandler}
        confirmTurn={confirmTurnHandler}
        nextLeg={nextLegHandler}
      />
    </div>
  );
};

export default DartsScorer;
