import React from "react";
import "./index.css";

const RoomPrompt = () => {
  const [code, _setCode] = React.useState("");
  const [randomCode] = React.useState(
    Math.floor(Math.random() * 1e12)
      .toString(16)
      .toUpperCase()
  );

  const setCode = (text) => {
    _setCode(text.toUpperCase());
  };

  return (
    <div class="prompt-container">
      <div className="prompt-text">Room Code:</div>
      <div className="prompt-input-container">
        <input className="prompt-input" type="text" value={code} onChange={(e) => setCode(e.target.value)} />
        {code ? (
          <a className="prompt-input-button" href={`/room/${code}`}>
            Join
          </a>
        ) : (
          <div className="prompt-input-button prompt-button-inactive">Join</div>
        )}
      </div>

      <div className="prompt-random">
        I don't have a room code,{" "}
        <a className="prompt-random-link" href={`/room/${randomCode}`}>
          let me try it with a random one.
        </a>
      </div>
    </div>
  );
};

export default RoomPrompt;
