import React from "react";

const DartsScorerBox = ({ dart }) => {
  if (dart === undefined) {
    return <div className="darts-scorer-current-turn-box"></div>;
  }

  let classNames = "darts-scorer-current-turn-box ";
  switch (dart.multiple) {
    case 1:
      classNames += "darts-scorer-current-turn-box-single";
      break;

    case 2:
      classNames += "darts-scorer-current-turn-box-double";
      break;

    default:
      classNames += "darts-scorer-current-turn-box-triple";
      break;
  }

  return <div className={classNames}>{dart.display}</div>;
};

const CurrentTurn = ({ currentThrowerName, isComplete, nextLeg, darts, eraseDart, undoTurn, confirmTurn }) => {
  if (isComplete) {
    return (
      <div className="darts-scorer-current-turn-container">
        <button className="darts-scorer-current-turn-erase" onClick={undoTurn}>
          Undo
        </button>

        <button className="darts-scorer-current-turn-next" onClick={nextLeg}>
          Next Leg
        </button>
      </div>
    );
  }
  return (
    <div className="darts-scorer-current-turn-container">
      {darts.length > 0 && (
        <button className="darts-scorer-current-turn-erase" onClick={eraseDart}>
          Erase
        </button>
      )}
      {darts.length === 0 && (
        <button className="darts-scorer-current-turn-erase" onClick={undoTurn}>
          Undo
        </button>
      )}
      <div className="darts-scorer-current-turn-boxes">
        <DartsScorerBox dart={darts[0]} />
        <DartsScorerBox dart={darts[1]} />
        <DartsScorerBox dart={darts[2]} />
      </div>
      <button className="darts-scorer-current-turn-confirm" onClick={confirmTurn}>
        Confirm
        <div className="darts-scorer-current-turn-confirm-thrower">({currentThrowerName})</div>
      </button>
    </div>
  );
};

export default CurrentTurn;
