import React from "react";

const HeaderPlayer = ({ active, score, isWinner, name, setName }) => {
  let classNames = "darts-scorer-player";
  if (active) {
    classNames += " darts-scorer-player-active";
  }
  if (isWinner) {
    classNames += " darts-scorer-player-winner";
  }

  return (
    <div className={classNames}>
      <input className="darts-scorer-player-name" type="text" onChange={(e) => setName(e.target.value)} value={name} />
      <div className="darts-scorer-player-score">{score}</div>
      {active && (
        <div className="darts-scorer-player-active-icon">
          <span role="img" aria-label="Current thrower">
            🎯
          </span>
        </div>
      )}
    </div>
  );
};

export default HeaderPlayer;
