import React from "react";
import HeaderPlayer from "./HeaderPlayer";
import StatsBox from "./StatsBox";

const Header = ({ state, setNames }) => {
  return (
    <div className="darts-scorer-header">
      <HeaderPlayer
        isWinner={state.complete && state.winner === 0}
        side={0}
        active={state.currentThrow === 0}
        score={state.players[0].points}
        name={state.names[0]}
        setName={(name) => {
          setNames(name, state.names[1]);
        }}
      />
      <HeaderPlayer
        isWinner={state.complete && state.winner === 1}
        side={1}
        active={state.currentThrow === 1}
        score={state.players[1].points}
        name={state.names[1]}
        setName={(name) => {
          setNames(state.names[0], name);
        }}
      />
      {state.legs[0] || state.legs[1] ? (
        <div className="darts-scorer-header-legs">
          {state.legs[0]} - {state.legs[1]}
        </div>
      ) : null}
      <StatsBox p0={state.stats[0]} p1={state.stats[1]} />
    </div>
  );
};

export default Header;
