import React from "react";

const markDisplay = (marks) => {
  switch (marks) {
    case 0:
      return "";
    case 1:
      return "/";
    case 2:
      return "X";
    default:
      return "O";
  }
};

const MarkboardButtons = ({ m0, m1, name, clickDart, sector }) => {
  if (m0 >= 3 && m1 >= 3) {
    return <div className="darts-scorer-markboard-row-closed">{name}</div>;
  }

  return (
    <div className="darts-scorer-markboard-row-open">
      <button
        className="darts-scorer-markboard-button darts-scorer-markboard-button-single"
        onClick={() => {
          clickDart({ sector, multiple: 1, display: `${name}` });
        }}
      >{`${name}`}</button>

      <button
        className="darts-scorer-markboard-button darts-scorer-markboard-button-double"
        onClick={() => {
          clickDart({ sector, multiple: 2, display: `D${name}` });
        }}
      >{`D${name}`}</button>

      {name !== "B" && (
        <button
          className="darts-scorer-markboard-button darts-scorer-markboard-button-triple"
          onClick={() => {
            clickDart({ sector, multiple: 3, display: `T${name}` });
          }}
        >{`T${name}`}</button>
      )}
    </div>
  );
};

const MarkboardRow = (props) => {
  const { m0, m1, activePlayer } = props;
  return (
    <div className="darts-scorer-markboard-row">
      <div className={`darts-scorer-markboard-marks ${activePlayer === 0 ? "darts-scorer-markboard-active" : ""}`}>
        {markDisplay(m0)}
      </div>
      <div className="darts-scorer-markboard-row-internal">
        <MarkboardButtons {...props} />
      </div>
      <div className={`darts-scorer-markboard-marks ${activePlayer === 1 ? "darts-scorer-markboard-active" : ""}`}>
        {markDisplay(m1)}
      </div>
    </div>
  );
};

export default MarkboardRow;
