const newStatePlayer = () => ({
  points: 0,
  marks: [0, 0, 0, 0, 0, 0, 0],
});

const newStateStats = () => ({
  turns: 0,
  hits: 0,
  marks: 0,
  mpr: 0,
  hitRate: 0,
});

export const newMatchState = () => ({
  tx: 0,
  legs: [0, 0],
  firstThrow: 0,
  currentThrow: 0,
  players: [newStatePlayer(), newStatePlayer()],
  stats: [newStateStats(), newStateStats()],
  names: ["Player 1", "Player 2"],
  pastTurns: [],
});

export const setNames = (match, n1, n2) => {
  match.names = [n1, n2];
  match.tx = match.tx + 1;
  return match;
};

const calculatePlayerScore = (playerMarks) => {
  return [20, 19, 18, 17, 16, 15, 25].reduce((mem, sectorValue, idx) => {
    return mem + Math.max(0, playerMarks[idx] - 3) * sectorValue;
  }, 0);
};

const isWinner = (currentPlayer, opponent) => {
  return currentPlayer.points >= opponent.points && currentPlayer.marks.every((sector) => sector >= 3);
};

const addTurnStats = (stats, turn) => {
  const turns = stats.turns + 1;
  const hits = turn.reduce((mem, dart) => {
    if (dart.miss || dart.effectiveMarks === 0) {
      return mem;
    }

    return mem + 1;
  }, stats.hits);

  const marks = turn.reduce((mem, dart) => {
    if (dart.miss) {
      return mem;
    }

    return mem + dart.effectiveMarks;
  }, stats.marks);

  return {
    turns,
    hits,
    marks,
    mpr: marks / turns,
    hitRate: hits / (turns * 3),
  };
};

const undoTurnStats = (stats, turn) => {
  const turns = stats.turns - 1;
  const hits = turn.reduce((mem, dart) => {
    if (dart.miss || dart.effectiveMarks === 0) {
      return mem;
    }

    return mem - 1;
  }, stats.hits);

  const marks = turn.reduce((mem, dart) => {
    if (dart.miss) {
      return mem;
    }

    return mem - dart.effectiveMarks;
  }, stats.marks);

  return {
    turns,
    hits,
    marks,
    mpr: marks / turns || 0,
    hitRate: hits / (turns * 3) || 0,
  };
};

export const applyTurn = (match, turn) => {
  match.tx += 1;
  const currentPlayer = match.players[match.currentThrow];
  const opponent = match.players[(match.currentThrow + 1) % 2];
  turn = turn.map((dart) => {
    if (dart.miss) {
      return dart;
    }

    if (opponent.marks[dart.sector] >= 3) {
      dart.effectiveMarks = Math.min(3 - currentPlayer.marks[dart.sector], dart.multiple);
      currentPlayer.marks[dart.sector] = Math.min(3, currentPlayer.marks[dart.sector] + dart.multiple);
    } else {
      dart.effectiveMarks = dart.multiple;
      currentPlayer.marks[dart.sector] = currentPlayer.marks[dart.sector] + dart.multiple;
    }

    return dart;
  });

  currentPlayer.points = calculatePlayerScore(currentPlayer.marks);

  match.stats[match.currentThrow] = addTurnStats(match.stats[match.currentThrow], turn);
  match.pastTurns = [...match.pastTurns, turn];

  if (isWinner(currentPlayer, opponent)) {
    match.winner = match.currentThrow;
    match.legs[match.winner] += 1;
    match.complete = true;
    return match;
  }

  match.players[match.currentThrow] = currentPlayer;
  match.currentThrow = (match.currentThrow + 1) % 2;
  return match;
};

export const nextLegState = (match) => {
  match.tx += 1;
  match.players = [newStatePlayer(), newStatePlayer()];
  match.firstThrow = match.currentThrow = (match.firstThrow + 1) % 2;
  match.complete = false;
  match.winner = undefined;
  match.pastTurns = [];
  return match;
};

export const undoTurn = (match) => {
  const turn = match.pastTurns.pop();
  if (!turn) {
    return { turn: [], state: match };
  }

  match.tx += 1;

  match.currentThrow = (match.currentThrow + 1) % 2;
  if (match.complete) {
    match.currentThrow = (match.currentThrow + 1) % 2;
    match.complete = false;
    match.legs[match.winner] -= 1;
    match.winner = undefined;
  }

  const currentPlayer = match.players[match.currentThrow];

  turn.forEach((dart) => {
    if (dart.miss) {
      return;
    }

    currentPlayer.marks[dart.sector] = currentPlayer.marks[dart.sector] - dart.effectiveMarks;
  });

  currentPlayer.points = calculatePlayerScore(currentPlayer.marks);
  match.stats[match.currentThrow] = undoTurnStats(match.stats[match.currentThrow], turn);

  const undoneTurn = turn
    .filter((dart) => !dart.miss)
    .map(({ sector, multiple, display }) => ({ sector, multiple, display }));

  match.players[match.currentThrow] = currentPlayer;
  return {
    turn: undoneTurn,
    state: match,
  };
};
